.header_title {
  margin-bottom: 15px;
  font-size: 20px;
}

.avatar_container {
  width: 90%;
  display: flex;
  justify-content: center;
  margin: 20px auto;
  padding-top: 15px;
  text-align: center;
}

.avatar_imageBox {
  width: 100px;
  height: 100px;
  border: 2px solid #293677;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
  &.active {
    background-color: #b1b2ff;
  }
}

.avatar_imageBox:hover {
  cursor: pointer;
  filter: brightness(1.1);
}

.avatar_image {
  width: 100%;
  height: 110%;
  -webkit-user-drag: none;
}
.avatar_imageBox:active {
  filter: brightness(1);
}

.name_label_wrapper {
  margin: 10px 0 10px;
}

.name_label {
  color: #293677;
}

.name_input {
  width: 200px;
  height: 30px;
  border: 1.5px solid #293677;
  border-radius: 15px;
  text-align: center;
  outline: none;
}

.submit_button {
  margin: 40px 0 -40px;
  height: 40px;
  font-size: 1.1rem;
  color: white;
}
